import React from "react";
import {FriendsItemType} from "../../../App";
export type FriendsCompType = {
    friends:FriendsItemType[]
}
export const Friends = (props:FriendsCompType) => {
    let mappedFriends = props.friends.map(el => {
        return (
            <li key={el.id}>{el.nameItem}</li>
        )
    })
    return(
        <div>
            <h3>Friends</h3>
            <ul>
                { mappedFriends }
            </ul>
        </div>

    )
}