import React from "react";
import {UserPageType} from "../../../reducers/usersReducer";
import {v1} from "uuid";

export type UsersType = {
    followUsers: (idUser: string) => void
    unfollowUsers: (idUser: string) => void
    setUsers: (users: UserPageType[]) => void
    users: UserPageType[]
}
export const Users = (props: UsersType) => {
    if (props.users.length === 0) {
        props.setUsers([
            {id:v1(),
                avatar:'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS06QFk-79tnLNs_GwwiJ-HaHTGRd5lPY7aoQ&usqp=CAU',
                followed:false,
                fullName:'Stefi',
                status:'How are you',
                location:{country:'Poland', city:'Warsaw'}
            },
            {id:v1(),
                avatar:'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS06QFk-79tnLNs_GwwiJ-HaHTGRd5lPY7aoQ&usqp=CAU',
                followed:true,
                fullName:'Sonia',
                status:'Hi',
                location:{country:'Germany', city:'Bonn'}
            },
            {id:v1(),
                avatar:'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS06QFk-79tnLNs_GwwiJ-HaHTGRd5lPY7aoQ&usqp=CAU',
                followed:true,
                fullName:'Lesia',
                status:'Yo yo',
                location:{country:'Belarus', city:'Minsk'}
            },
            {id:v1(),
                avatar:'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS06QFk-79tnLNs_GwwiJ-HaHTGRd5lPY7aoQ&usqp=CAU',
                followed:true,
                fullName:'Mary',
                status:'That is good',
                location:{country:'France', city:'Paris'}
            },
        ])
    }

    return (
        <div>
            {props.users.map(el => {

                return (
                    <div key={el.id}>
                        <span>
                            <h5>{el.fullName}</h5>
                        <div>
                            <img src={el.avatar} style={{width:'50px', height:'50px',borderRadius:'100%'}}/>
                        </div>
                            <div>{el.location.country}</div>
                            <div>{el.location.city}</div>
                            <span>{el.status}</span>
                        <div>
                           {el.followed
                               ? <button onClick={() => {props.unfollowUsers(el.id)}}>Unfollow</button>
                               : <button onClick={() => {props.followUsers(el.id)}}>Follow</button>
                           }
                        </div>
                            </span>
                    </div>
                )
            })}
        </div>

    )
}
