import React, {ChangeEvent} from "react";
import cl from './Posts.module.css'
import {Post,} from "./post/Post";
import {PostItemType} from "../../../App";
import {addPostAC, updateValuePostAC} from "../../../reducers/contentReducer";

export type PostsMainType = {
    posts: PostItemType[]
    changeValuePostContainer: (text: string) => void
    addPostContainer: () => void
    newValuePost: string
}
export const Posts = (props: PostsMainType) => {
    console.log(props.posts)
    let mappedMessages = props.posts.map((el, i) => {
        return (
            <Post
                key={`${el.id}+${i}`}
                post={el.post}
                like={el.like}
            />
        )
    })

    const addPost = () => {
        props.addPostContainer()
    }

    const changeValuePost = (e: ChangeEvent<HTMLTextAreaElement>) => {
        props.changeValuePostContainer(e.currentTarget.value)
    }
    return (
        <div className={cl.posts}>
            <h3 className={cl.titlePosts}>my posts</h3>
            <div>
                <textarea value={props.newValuePost} onChange={changeValuePost}/>
            </div>
            <div className={cl.btnAdd}>
                <button onClick={addPost}>add post</button>
            </div>
            {mappedMessages}
        </div>

    )
}