const mapStateToProps = (state:AppRootStateType) => {
    console.log(state.contentPage.newValuePost)
    return {
        posts:state.contentPage.posts,
        newValuePost:state.contentPage.newValuePost
    }
}
const mapDispatchToProps = (dispatch:Dispatch) => {
    return {
        addPostContainer:() => {
            dispatch(addPostAC())
        },
        changeValuePostContainer:(newValuePost:string) => {
            dispatch(updateValuePostAC(newValuePost))
        }

    }
}
export const SuperContainerPosts = connect(mapStateToProps,mapDispatchToProps)(Posts)