const mapStateToProps = (state:AppRootStateType) => {

    return {
        dialogs: state.messagesPage.dialogs,
        messages: state.messagesPage.messages,
        newValueMessage:state.messagesPage.newValueMessage
    }
}
const mapDispatchToProps = (dispatch:Dispatch) => {
    return {
        changeMessageValueContainer:(newValueMessage:string) => dispatch(updateValueMessageAC(newValueMessage)),
        addMessageContainer:() => dispatch(addMessageAC())
    }
}
export const SuperContainerDialogs = connect(mapStateToProps,mapDispatchToProps)(Dialogs)