import cl from "./Dialogs.module.css";
import {NavLink} from "react-router-dom";
import React from "react";

export type ItemDialogType = {
    nameItem: string
    id: string
    avatar: string
}
export const ItemDialog = (props: ItemDialogType) => {
    let path = '/dialogs/' + props.id
    return (
        <div className={cl.itemDialog}>
            <NavLink to={path} style={({isActive}) => {
                return {color: isActive ? 'white' : 'yellow', textDecoration: 'none'}
            }}>
                <div style={{display:'flex', alignItems:'center'}} >
                <img src={props.avatar} style={{width:'30px', borderRadius:'15px'}}/>
                    <span style={{marginLeft:'5px'}}>{props.nameItem}</span>
                </div>
            </NavLink>
        </div>
    )
}