import React from "react";
import {FriendsItemType, StateType, StoreType} from "../../../App";
import {Friends} from "./Friends";
import {ReduxStoreType} from "../../../reduxStore/reduxStore";
import {StoreContext} from "../../../Provider";

export type ContainerFriendsType = {
    /*store:ReduxStoreType*/
}
export const ContainerFriends = (props: ContainerFriendsType) => {
    return (<StoreContext.Consumer>
            {
                (store) => {
                    let friends = store.getState().sideBarPage.friends
                    return <Friends
                        friends={friends}
                    />
                }
            }

        </StoreContext.Consumer>
    )
}