import React from 'react';
import {Header} from "./components/header/Header";
import {SideBar} from "./components/sideBar/SideBar";
import {Content} from "./components/content/Contant";
import cl from './App.module.css'
import {BrowserRouter, Route, Routes} from "react-router-dom";
import {Music} from "./components/sideBar/Music/Music";
import {News} from "./components/sideBar/News/News";
import {Setting} from "./components/sideBar/Setting/Setting";
import {ActionType} from "./state/state";
import { SuperContainerDialogs} from "./components/Dialogs/ContainerDialogs";
import {ReduxStoreType} from "./reduxStore/reduxStore";
import {Dispatch} from 'redux'
import {Users} from "./components/sideBar/users/Users";
import {SuperContainerUsers} from "./components/sideBar/users/SuperContainerUsers";

export type PostItemType = {
    id: string
    like: number
    post:string
}

export type AppPartType = {
    contentPage: PostType
    messagesPage: DialogsType & MessagesType
    sideBarPage:FriendsType
}
export type StoreType = {
    store:ReduxStoreType
    dispatch: Dispatch
}
export type StateType ={
    state:AppPartType
    dispatch:(action: ActionType)=> void
    newValuePost:string
    newValueMessage: string
}

export type PostType = {
    posts:Array<PostItemType>
    newValuePost:string
}
export type DialogsItemType = {
    id: string
    nameItem:string
    avatar:string
}
export type DialogsType = {
    dialogs:Array<DialogsItemType>
}
export type FriendsItemType = {
    id: string
    nameItem:string
}
export type FriendsType = {
    friends:Array<FriendsItemType>
}
export type MessagesType = {
    messages:Array<MessagesItemType>
    newValueMessage: string
}
export type MessagesItemType = {
    id: string
    message:string
}
function App() {
 //const state = props.store.getState()
    // state: AppRootStateType
    // dispatch: Dispatch (import from 'redux')
    return (
        <BrowserRouter>
            <div className={cl.appWrapper}>
                <Header/>
                <SideBar />
                <div className={cl.appWrapperContent}>
                    <Routes>
                        <Route path={'/content'} element={<Content/>}/>
                        <Route path={'/dialogs'} element={
                            <SuperContainerDialogs/>}
                        />
                        <Route path={'/news'}  element={<News/>}/>
                        <Route path={'/setting'} element={<Setting/>}/>
                        <Route path={'/music'} element={<Music/>}/>
                        <Route path={'/users'} element={<SuperContainerUsers/>}/>
                    </Routes>

                </div>
            </div>
        </BrowserRouter>
    );
}

export default App;
