import {PostType} from "../App";
import {ActionType} from "../state/state";
import {v1} from "uuid";
export type AddPostType = {
    type: 'ADD-POST'
}
export const addPostAC = (): AddPostType => {
    return {
        type: 'ADD-POST'
    } as const
}
export type UpdateValuePostType = {
    type: 'UPDATE-VALUE-POST'
    newValue: string
}
export const updateValuePostAC = (newValue: string): UpdateValuePostType => {
    return {
        type: 'UPDATE-VALUE-POST',
        newValue
    } as const
}
export type ActionContentType = AddPostType | UpdateValuePostType
let initialState: PostType = {
    posts: [
        {id: '1', like: 10, post: 'Super'},
        {id: '2', like: 15, post: 'Waw, hey'},
        {id: '3', like: 17, post: 'Yo, like'},
        {id: '4', like: 20, post: 'Yo, super?'}
    ],
    newValuePost: 'it-kamasutra',
}
export const contentReducer = (state=initialState, action: ActionType):PostType => {
    switch (action.type) {

        case "ADD-POST": {
            let newPost = {id: v1().toString(), like: 20, post: state.newValuePost};
            //state.newValuePost = ''
            return  {...state, posts: [...state.posts, newPost],newValuePost:''}
        }

        case 'UPDATE-VALUE-POST': {
            state.newValuePost = action.newValue
            return {...state,newValuePost:action.newValue}
        }
        default : {
            return state
        }
    }
}

