import React from "react";
import cl from './SideBar.module.css'
import {NavLink} from "react-router-dom";
import {ContainerFriends} from "./Friends/ContainerFriends";
import {ReduxStoreType} from "../../reduxStore/reduxStore";
export type SideBarCompType = {
    store:ReduxStoreType
}

export const SideBar = () => {

    return (<div>

        <nav className={cl.nav}>
            <NavLink to={'/content'} className={cl.textLink} style={({isActive}) =>{
                return {
                color:isActive ? 'white' :'',textDecoration: 'none'
            }}}>
                <div>Content</div>
            </NavLink>

            <NavLink to={'/dialogs'} className={cl.textLink} style={({isActive}) =>{
                return {
                    color:isActive ? 'white' :'',textDecoration: 'none'
                }}}>
                <div>Messages</div>
            </NavLink>

            <NavLink to={'/news'} className={cl.textLink} style={({isActive}) =>{
                return {
                    color:isActive ? 'white' :'',textDecoration: 'none'
                }}}>
                <div>News</div>
            </NavLink>

            <NavLink to={'/setting'} className={cl.textLink} style={({isActive}) =>{
                return {
                    color:isActive ? 'white' :'',textDecoration: 'none'
                }}}>
                <div>Setting</div>
            </NavLink>

            <NavLink to={'/music'} className={cl.textLink} style={({isActive}) =>{
                return {
                    color:isActive ? 'white' :'',textDecoration: 'none'
                }}}>
                <div>Music</div>
            </NavLink>
            <NavLink
                to={'/users'}
                className={cl.textLink}
                style={({isActive}) => { return {
                    color:isActive ?'white':'',
                    textDecoration:"none"
                }  }}>
                <div>Find users</div>
            </NavLink>
        </nav>
            <ContainerFriends /*store={props.store}*//>
        </div>
    )
}