import React from "react";
import cl from './Post.module.css'
export type PostCompType = {
    like: number
    post:string
}
export const Post = (props:PostCompType) => {
    return (
            <div className={cl.post}>
                <img className={cl.imgAvaPost} src={'https://seeklogo.com/images/L/lady-face-logo-42390A330E-seeklogo.com.png'}/>
                {props.post}
                <img  className={cl.imgLike} src={'https://thumbs.dreamstime.com/b/logo-likee-like-video-to-globalna-platforma-do-tworzenia-i-udost%C4%99pniania-kr%C3%B3tkich-film%C3%B3w-prawdopodobnie-s%C5%82ynna-aplikacja-199405719.jpg'}/>
                {props.like}
            </div>

)
}