import {v1} from "uuid";

export type UsersType = {
    users: UserPageType[]
}
export type UserPageType = {
    id:string
    avatar:string
    followed:boolean
    fullName:string
    status:string
    location:{country:string, city:string}
}
const initialState:UsersType  = { users:[]}


export type FollowType = ReturnType<typeof followAC>
export const followAC = (idUser:string ) => {
    return {
        type:'FOLLOW',
        idUser,
    } as const
}

export const unfollowAC = (idUser:string ) => {
    return {
        type:'UNFOLLOW',
        idUser,
    } as const
}
export type UnfollowType = ReturnType<typeof unfollowAC>
export const setUsersAC = (users:UserPageType[]) => {
    return {
        type:'SET-USERS',
        users,
    } as const
}
export type SetUserType = ReturnType<typeof setUsersAC>
export type ActionUsersType = FollowType | UnfollowType | SetUserType
export const  usersReducer = (state= initialState, action:ActionUsersType):UsersType => {
    switch (action.type) {
        case 'FOLLOW': {
            return {...state,users:state.users.map(el => el.id === action.idUser ? {...el,followed:true}: el)}
        }
        case 'UNFOLLOW': {
            return {...state,users:state.users.map(el => el.id === action.idUser ? {...el,followed:false}: el)}
        }
        case 'SET-USERS': {
            return {...state,users: [...state.users,...action.users]}
        }
        default : {
            return state
        }
    }
}