import {connect} from "react-redux";
import {Users} from "./Users";
import {AppRootStateType} from "../../../reduxStore/reduxStore";
import {Dispatch} from "redux";
import {followAC, setUsersAC, unfollowAC, UserPageType} from "../../../reducers/usersReducer";
const mapStateToProps = (state:AppRootStateType) => {
    return {
        users: state.usersPage.users
    }
}
const dispatchStateToProps = (dispatch:Dispatch) => {
    return {
        followUsers:(idUser:string) => dispatch(followAC(idUser)),
        unfollowUsers:(idUser:string) => dispatch(unfollowAC(idUser)),
        setUsers:(users:UserPageType[]) => dispatch(setUsersAC(users))
    }
}
export const SuperContainerUsers = connect(mapStateToProps, dispatchStateToProps)(Users)