import {DialogsType, MessagesType} from "../App";
import {ActionType} from "../state/state";
import {v1} from "uuid";

export type UpdateValueMessageType = {
    type: 'UPDATE-VALUE-MESSAGE'
    newValueMessage: string
}
export const updateValueMessageAC = (newValueMessage: string): UpdateValueMessageType => {
    return {
        type: 'UPDATE-VALUE-MESSAGE',
        newValueMessage: newValueMessage
    } as const
}
export type AddMessageType = {
    type: 'ADD-MESSAGE'
}
export const addMessageAC = (): AddMessageType => {
    return {
        type: 'ADD-MESSAGE'
    } as const
}
//export type ActionMessageType = UpdateValueMessageType | AddMessageType
let initialState: DialogsType & MessagesType = {
    dialogs: [
        {
            id: '1',
            nameItem: 'Nata',
            avatar: 'data:image/jpeg;base64,'
        },
        {
            id: '2',
            nameItem: 'Jack',
            avatar: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQNp0kLskDZV0BnD4bz5mZVmAQRC7RLQaNWpQ&usqp=CAU'
        },
        {
            id: '3',
            nameItem: 'Sasha',
            avatar: 'data:image/jpeg;base64,'
        },
        {
            id: '4',
            nameItem: 'Sonia',
            avatar: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRWv5agAt-6uyD84zhS1ju1a4pwtHMJ0EBurA&usqp=CAU'
        }

    ],
    messages: [
        {id: '1', message: 'Hi'},
        {id: '2', message: 'How are you'},
        {id: '3', message: 'Yo, yo'},
        {id: '4', message: 'Yo, yo?'}
    ],
    newValueMessage: 'add message',
}
export const messageReducer = (state = initialState, action: ActionType): DialogsType & MessagesType => {
    switch (action.type) {

        case "ADD-MESSAGE": {
            let newMessage = {id: v1(), message: state.newValueMessage}
            //state.messages.push(newMessage)
            //state.newValueMessage = ''
            return {...state,messages:[...state.messages,newMessage],newValueMessage: ''}
        }
        case "UPDATE-VALUE-MESSAGE": {
            //state.newValueMessage = action.newValueMessage
            return {...state,newValueMessage:action.newValueMessage}
        }
        default : {
            return state
        }

    }
}