import React, {ChangeEvent} from "react";
import cl from "./Dialogs.module.css"
import {ItemDialog} from "./ItemDialog";
import {Message} from "./Message";
import {DialogsItemType, MessagesItemType} from "../../App";

export type DialogsCompType = {
    dialogs: DialogsItemType[]
    messages: MessagesItemType[]
    changeMessageValueContainer:(text:string) => void
    addMessageContainer:() => void
    newValueMessage: string
}
export const Dialogs = (props: DialogsCompType) => {
    let mappedPosts = props.messages.map(el => {
        return (
            <Message
                key={el.id}
                message={el.message}
            />
        )
    })
    let mappedState = props.dialogs.map(el => {
        return (
            <ItemDialog
                key={el.id}
                nameItem={el.nameItem}
                avatar={el.avatar}
                id={el.id}
            />
        )
    })

    const changeMessageValue = (e:ChangeEvent<HTMLTextAreaElement>) => {
        props.changeMessageValueContainer(e.currentTarget.value)
    }
    const addMessage = () => {
        props.addMessageContainer()
    }

    return (
        <nav className={cl.dialogsMain}>
            <div className={cl.dialogsItems}>
                Dialogs
                {mappedState}
            </div>
            <div className={cl.messages}>Messages
                {mappedPosts}
            </div>
            <div><textarea value={props.newValueMessage} onChange={changeMessageValue}/></div>
            <div>
                <button onClick={addMessage}>add message</button>
            </div>
        </nav>
    )
}